import React from 'react'
import { Link } from 'react-router-dom';
import Services3SectionGallery from './sections/section_services3/Services3SectionGallery';
import Services3SectionPricingTable from './sections/section_services3/Services3SectionPricingTable';
var two = require('../../assets/images/banner/2.jpg');
const ServicesDetailPage = (props) => {
	  	
    return(
	<>
		{/* <!-- INNER PAGE BANNER --> */}
		<div className="wt-bnr-inr overlay-wraper bg-center" style={{backgroundImage:`url(${two})`}}>
			<div className="overlay-main site-bg-primary opacity-09"></div>
			<div className="container">
				<div className="wt-bnr-inr-entry">
					<div className="banner-title-outer">
						<div className="banner-title-name">
							<h2 className="site-text-white">Service Detail</h2>
						</div>
					</div>
					{/* <!-- BREADCRUMB ROW --> */}                            
					
						<div>
							<ul className="wt-breadcrumb breadcrumb-style-2">
								<li><Link to="/">Home</Link></li>
								<li>Service Detail</li>
							</ul>
						</div>
					
					{/* <!-- BREADCRUMB ROW END --> */}                        
				</div>
			</div>
		</div>			
		{/* <!-- INNER PAGE BANNER END --> */}
		<Services3SectionGallery />
		<Services3SectionPricingTable />
    </>
    )
}
export default ServicesDetailPage;